
.user-details {
  padding: 20px;
  margin: 0 0 20px 0;
}

.user-name {
  margin: 0 0 20px 0;
  font-size: 22px;
}

.user-coins-count {
  margin: 10px 0 0 0;
  display: flex;
  align-items: center;
}

.user-coins-rate {
  margin: 10px 0 0 0;
  display: flex;
  align-items: center;
}

.user-coins-count img, .user-coins-rate img {
  margin: 0 5px 0 0;
}


.chart {
  margin: 20px 0;
}

.chart-name {
  margin: 5px 0;
}

.chart-desc {
  font-size: 0.8rem;
}
.statistic-chart:not(:last-child) {
  margin: 0 0 20px 0;
  border-bottom: 1px solid #f4f5f7;
}
.contact-preview {
  display: inline-block;
  padding: 0 25px;
  height: 50px;
  width: 240px;
  font-size: 17px;
  line-height: 50px;
  border-radius: 25px;
  background-color: #f1f1f1; 
}
  
.contact-preview img {
  float: left;
  margin: 0 10px 0 -25px;
  height: 50px;
  width: 50px;
  border-radius: 50%; 
}

.contacts-list-item {
  margin: 10px 0; 
}

.contacts-page {
  position: relative;
  height: 100%; 
}

.contacts-page .search-container {
  height: 40px;
  padding: 0 10px; 
}

.contacts-page .contacts-container {
  max-height: 360px;
  overflow-x: auto; 
}

.contacts-page .action-container {
  position: absolute;
  bottom: -3px;
  right: 10px;
}

.contact-details-header {
  display: flex;
  justify-content: space-between;
  height: 40px; 
}
.contact-details-header a:not(:last-child) {
  margin: 0 10px 0 0; 
}

.contact-details .contact-details-row {
  margin: 10px 0 0 0; 
}

.moves-list-container, .transter-coins-container {
  margin: 20px 0;
}
.contact-edit-header {
    display: flex;
    justify-content: space-between;
    height: 40px; 
  }
  .contact-edit-header a:not(:last-child) {
    margin: 0 10px 0 0; 
  }
  
  .contact-edit .contact-edit-row {
    margin: 10px 0 0 0; 
  }
  
  .moves-list-container, .transter-coins-container {
    margin: 20px 0;
  }

.app {
  max-width: 300px;
  padding: 20px;
  border: 1px solid black;
  border-radius: 5px;
  margin: 30px auto; 
}

.app .app-content {
  padding: 20px 0 0 0;
  height: 400px;
  overflow-x: hidden;
  overflow-y: auto; 
}

.app [class^="flaticon-"]:before,
.app [class*=" flaticon-"]:before,
.app [class^="flaticon-"]:after,
.app [class*=" flaticon-"]:after {
    margin: 0; 
}

body {
    margin: 0;
    padding: 0;
}

a, a:visited, a:hover, a:active {
    text-decoration: none;
    color: inherit; 
}

ul {
    margin: 0;
    padding: 0; 
}
  
li {
    list-style: none; 
}
